import { connect } from 'react-redux';
import CartData from './CartData';
import purchaseBooks  from '../../store/cartList/cartListOperations';
import  {getCartList}  from '../../store/cartList/cartListSelectors';
import {isOpenModal}  from '../../store/modalControllers/modalControllersActions';

import modalOpen  from '../../store/modalControllers/modalControllersSelectors';

const mapStateToProps = state => ({
  books: getCartList(state),
  modalOpen: modalOpen(state),
});


const mapDispatchToProps = { isOpenModal ,handlePurchase: purchaseBooks };


export default connect(mapStateToProps,mapDispatchToProps)(CartData);
