import { connect } from 'react-redux';
import FilterForm from './FilterForm';
import { getTitleBook, getPrice } from '../../store/filters/filtersSelectors';
import { ActionTypes, setFilter } from '../../store/filters/filtersActions';

const mapStateToProps = state => ({
  title: getTitleBook(state),
  price: getPrice(state),
});

const mapDispatchToProps = dispatch => ({
  onChange: ({ target: { value, name } }) => {
    let type;
    switch (name) {
      case 'title':
        type = ActionTypes.SEARCH_BY_NAME;
        break;
      case 'price':
        type = ActionTypes.FILTER_PRICE;
        break;
      default:
        return;
    }
    dispatch(setFilter(type, value));
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(FilterForm);
