import { connect } from 'react-redux';
import BookData from './BookData';
import { getBookDetailsLoading } from '../../store/loading/loadingSelectors';
import  getBook  from '../../store/bookDetail/bookDetailOperations';
import { deleteDetailsBookFromStore } from '../../store/bookDetail/bookDetailActions';


const mapStateToProps = state => ({
  isLoading: getBookDetailsLoading(state),
});

const mapDispatchToProps = {
  getBook,
  deleteDetailsBookFromStore,
};

export default connect(mapStateToProps, mapDispatchToProps)(BookData);
